div.desktopOnly .homeBanner {
  display: flex;
  flex-direction: column;
  align-items: center;
}

div.desktopOnly .h1_home {
  margin-bottom: 12px;
}

.form_create-task {
  display: flex;
  align-items: center;
  margin-bottom: 18px;
}
.form_field {
  border-radius: 7px;
  margin: 0 4px;
}

div.desktopOnly h2.task_feed_title {
  color: #1c1e1e;
}
div.header_task-list {
  display: flex;
}
div.desktopOnly h3.header_title {
  width: 15ch;
  font-size: 16px;
  font-weight: 600;
  color: #1c1e1e;
}

.buffer_container {
  display: flex;
}
.buffer_task-list {
  display: flex;
  flex-direction: column;
}
.buffered_task {
  display: flex;
  border-bottom: 1px solid #d9d9d9;
}
.buffer {
  align-self: center;
}
.buffer.time {
  width: 15.3ch;
}
.buffer.description {
  width: 60ch;
}

div.row_item {
  border-bottom: 2px solid #e6e5f1;
  border-radius: 5px;
  margin-bottom: 10px;
  background-color: #fff;
  height: 20px;
  padding: 12px;
  margin-left: -12px;
}

.edit_panel {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.custom-checkbox {
  width: 10px;
  height: 10px;
  background-color: white;
  border-radius: 50%;
  border: 2px solid #a4a4a4;
  cursor: pointer;
  margin-right: 5vw;
  align-self: center;
}
.edit_button {
  margin: 3px;
}

